import "./App.css";
import { SearchBar } from "../SearchBar/SearchBar";
import { SearchResults } from "../SearchResults/SearchResults";
import { Playlist } from "../Playlist/Playlist";
import React from "react";

import { Spotify } from "../../util/Spotify";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      searchResults: [],
      playlistName: "",
      playlistTracks: [],
    };

    this.addTrack = this.addTrack.bind(this);
    this.removeTrack = this.removeTrack.bind(this);
    this.updatePlaylistName = this.updatePlaylistName.bind(this);
    this.savePlaylist = this.savePlaylist.bind(this);
    this.search = this.search.bind(this);
  }

  addTrack(track) {
    if (
      this.state.playlistTracks.filter((element) => element.id === track.id)
        .length > 0
    ) {
      return;
    } else {
      const updatedPlaylistTracks = this.state.playlistTracks;
      updatedPlaylistTracks.push(track);

      const updatedSearchResults = this.state.searchResults;
      const index = updatedSearchResults.indexOf(track);
      if (index > -1) {
        updatedSearchResults.splice(index, 1);
      }
      this.setState({
        playlistTracks: updatedPlaylistTracks,
        searchResults: updatedSearchResults,
      });
    }
  }

  removeTrack(track) {
    const updatedPlaylistTracks = this.state.playlistTracks;
    const index = updatedPlaylistTracks.indexOf(track);
    if (index > -1) {
      updatedPlaylistTracks.splice(index, 1);
      const updatedSearchResults = this.state.searchResults;
      updatedSearchResults.unshift(track);
      this.setState({
        playlistTracks: updatedPlaylistTracks,
        searchResults: updatedSearchResults,
      });
    }
  }
  updatePlaylistName(name) {
    this.setState({ playlistName: name });
  }
  savePlaylist() {
    const trackURIs = [];
    this.state.playlistTracks.forEach((track) => {
      trackURIs.push(track.uri);
    });
    if (Spotify.savePlaylist(this.state.playlistName, trackURIs)) {
      this.setState({
        playlistName: "",
        playlistTracks: [],
      });
    }
  }
  async search(term) {
    const results = await Spotify.search(term);
    this.setState({
      searchResults: results,
    });
  }

  render() {
    return (
      <div className="App">
        <h1>Create new playlists on Spotifyy</h1>
        <SearchBar onSearch={this.search} />
        <div className="App-playlist">
          {this.state.searchResults && (
            <SearchResults
              searchResults={this.state.searchResults}
              onAdd={this.addTrack}
            />
          )}
          <Playlist
            title={this.state.playlistName}
            playlistTracks={this.state.playlistTracks}
            onRemove={this.removeTrack}
            onNameChange={this.updatePlaylistName}
            onSave={this.savePlaylist}
          />
        </div>
      </div>
    );
  }
}

export default App;
