const clientId = "1189bc0908be4896ae249e6491292789";
const redirectUri = "https://mariannefabre.github.io/Jammming-React-App/";

let userAccessToken;

export const Spotify = {
  getAccessToken() {
    if (userAccessToken) {
      return;
    } else {
      const finalUrl = window.location.href;
      const tokenRegex = /access_token=([^&]*)/;
      const expirationRegex = /expires_in=([^&]*)/;
      if (
        finalUrl.match(expirationRegex) != null &&
        finalUrl.match(tokenRegex) != null
      ) {
        let expirationTime = finalUrl.match(expirationRegex)[1];
        userAccessToken = finalUrl.match(tokenRegex)[1];
        window.setTimeout(() => {
          userAccessToken = "";
        }, expirationTime * 1000);
        window.history.pushState("Access Token", null, "/");
      } else if (finalUrl.match(tokenRegex) === null) {
        const url = "https://accounts.spotify.com/authorize?";
        const responseType = "token";
        const scope = "playlist-modify-public";
        const redirectUrl =
          url +
          "client_id=" +
          clientId +
          (scope ? "&scope=" + encodeURIComponent(scope) : "") +
          "&response_type=" +
          responseType +
          "&redirect_uri=" +
          redirectUri;
        window.location.href = redirectUrl;
      }
    }
  },

  async search(searchTerm) {
    this.getAccessToken();
    const urlToFetch =
      "https://api.spotify.com/v1/search?type=track&q=" + searchTerm;
    try {
      const response = await fetch(urlToFetch, {
        headers: {
          Authorization: `Bearer ${userAccessToken}`,
        },
      });
      if (response.ok) {
        const jsonResponse = await response.json();
        console.log(jsonResponse);
        const tracks = jsonResponse.tracks.items.map((track) => {
          return {
            name: track.name,
            artist: track.artists[0].name,
            album: track.album.name,
            id: track.id,
            uri: track.uri,
            previewUrl: track.preview_url,
          };
        });
        return tracks;
      } else {
        throw new Error("Request failed!");
      }
    } catch (e) {
      console.log(e);
    }
  },

  async savePlaylist(playlistName, trackURIs) {
    console.log(userAccessToken);
    if (!playlistName || !trackURIs) {
      return false;
    } else {
      // Get current user's profile
      const urlToFetch = "https://api.spotify.com/v1/me";
      try {
        let response = await fetch(urlToFetch, {
          headers: {
            Authorization: `Bearer ${userAccessToken}`,
          },
        });
        if (response.ok) {
          const jsonResponse = await response.json();
          const user_id = jsonResponse.id;

          // Create playlist in user's account
          const createPlaylistUrl =
            "	https://api.spotify.com/v1/users/" + user_id + "/playlists";
          try {
            const createPlaylistResponse = await fetch(createPlaylistUrl, {
              method: "post",
              headers: {
                Authorization: `Bearer ${userAccessToken}`,
              },
              body: JSON.stringify({
                name: playlistName,
              }),
            });
            if (createPlaylistResponse.ok) {
              response = await createPlaylistResponse.json();
              console.log(response);
              const playlistID = response.id;
              // Add items to playlist
              const addItemsUrl =
                "https://api.spotify.com/v1/playlists/" +
                playlistID +
                "/tracks?uris=" +
                trackURIs;
              try {
                const addItemsResponse = await fetch(addItemsUrl, {
                  method: "post",
                  headers: {
                    Authorization: `Bearer ${userAccessToken}`,
                    "Content-type": "application/json",
                  },
                });
                if (addItemsResponse.ok) {
                  return true;
                } else {
                  throw new Error("Could not add songs to playlist.");
                }
              } catch (e) {
                console.log(e);
                return false;
              }
            }
          } catch (e) {
            console.log(e);
            return false;
          }
        } else {
          throw new Error("Could not get user profile.");
        }
      } catch (e) {
        console.log(e);
        return false;
      }
    }
  },
};
